<template>
  <div class="hbhistory">
      <header>
          <router-link to="/benefit">
          <i class="el-icon-arrow-left"></i>
          </router-link>
          <div>历史红包</div>
      </header>
      <section class="scroll_container">
          <ul class="hongbao_list_ul">
              <li class="hongbao_list_li" v-for="h in hbArr" :key="h.id">
                  <section class="list_item">
                      <div class="list_item_left">
                          <span>¥</span>
                          <span>{{h.amount}}</span>
                          <span>.</span>
                          <span>0</span>
                          <p>满{{h.sum_condition}}元可用</p>
                      </div>
                      <div class="list_item_right">
                          <h4>{{h.name}}</h4>
                          <p>{{h.end_date}}到期</p>
                          <p>限收货手机号为 {{h.phone}}</p>
                      </div>
                  </section>
                  <div class="list_item_footer">
                          <p>限品类：小吃夜宵</p>
                      </div>
                      <i class="expired el-icon-error"></i>
              </li>
          </ul>
      </section>
  </div>
</template>

<script>
export default {
name:'Hbhistory',
data(){
            return{
                userid:'',
                hbArr:[]
            }
        },
        created(){
            this.userid = localStorage.getItem('userid');
            this.axios.get("/promotion/v2/users/"+this.userid+"/expired_hongbaos", {
                params: { user_id: this.userid,limit:10 }
                }).then(res => {
                this.hbArr = res;
                //  console.log(res)
                }).catch(err => {});
        }
}
</script>

<style>
.hbhistory{
    padding-top: 1.95rem;
    background-color: #f1f1f1;
}
header>a{
  position: absolute;
